<template>
  <div class="basis-full">
      <div class="max-w-7xl mx-auto my-10">
        <div class="flex justify-between">
          <h3 class="text-2xl">FAQ</h3>
          <div
            v-if="$store.state.uid == $store.state.adminUid"
            class="flex justify-end">
          <router-link to="/customerservice/qna/write" 
            class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
            글쓰기
          </router-link>
        </div>
        </div>
        <ul class="flex flex-col p-4 py-2 text-center font-bold">
          <li v-for="(e, i) in dataList" :key="e" :style="`${e}`">
            <h4> 
              <button @click="current = i" type="button" class="flex items-center justify-between w-full p-3 font-medium text-left text-gray-500 border border-b-0 border-gray-200 rounded-t-xl focus:ring-4 focus:ring-gray-200 dark:focus:ring-gray-800 dark:border-gray-700 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-800">
                <span class="flex items-center">
                  <!-- <svg class="w-5 h-5 mr-2 shrink-0" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-8-3a1 1 0 00-.867.5 1 1 0 11-1.731-1A3 3 0 0113 8a3.001 3.001 0 01-2 2.83V11a1 1 0 11-2 0v-1a1 1 0 011-1 1 1 0 100-2zm0 8a1 1 0 100-2 1 1 0 000 2z" clip-rule="evenodd"></path></svg> -->
                  <!-- svg 대체할 아이콘 찾기 -->
                   {{ e.title }}
                </span>
                <div 
                  v-if="$store.state.uid == $store.state.adminUid"
                  class="flex gap-2">
                  <router-link 
                    @click="$store.commit('QnaModify', dataId[i])"
                    to="/customerservice/qna/modify" 
                    class="bg-blue-400 hover:bg-blue-600 focus:ring-blue-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
                    수정
                  </router-link>
                  <button @click="Delete(i)"
                    class="bg-red-400 hover:bg-red-600 focus:ring-red-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
                    삭제
                  </button>
                </div>
                <!-- <svg class="w-6 h-6 rotate-180 shrink-0" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clip-rule="evenodd"></path></svg> -->
              </button>
            </h4>
            <div :class="current == i ? 'h-auto' : 'h-0'" class="overflow-hidden"> 
              <div class="p-4 border border-b-0 border-gray-200 dark:border-gray-700 dark:bg-gray-900">
                <p class="mb-2 text-gray-500 dark:text-gray-400 text-left">{{ e.content }}</p>
              </div>
            </div>
          </li>
        </ul>
      </div>
  </div>
</template>

<script>
  import { db } from '@/firebase'

  export default {
    name: 'QnaList',
    data() {
      return {
        current: 0,
        dataList: [],
        dataId: [],
      }
    },
    methods: {
      fetchPost(){
        const query = db.collection('qna').orderBy('date', 'asc')

        query.get().then((data)=>{
          const items = []
          const ids = []

          data.forEach(e=>{
            items.push(e.data())
            ids.push(e.id)
          })

          this.dataList = items
          this.dataId = ids
        })
      },
      Delete(i) {
        let msg = confirm('삭제된 데이터는 복수 할 수 없습니다. \r\r 삭제하시겠습니다?')
        if (msg) {
          db.collection('qna').doc(this.dataId[i]).delete().then(() => {
            alert('삭제가 완료되었습니다.');
            this.fetchPost()
          })
        }
      },
    },
    mounted() {
      this.fetchPost()
    },
  }
</script>