<template>
  <div class="py-24 text-center basis-full">
    <h3 class="text-3xl font-bold mb-5 relative after:absolute inline-block after:w-[80%] after:h-0.5 after:bg-slate-800 after:-bottom-2 after:left-2/4 after:-translate-x-2/4">로그인</h3>
  </div>
  <div class="pb-24 flex flex-col items-center">
    <form class="">
      <!--  id="email" name="email" required="" -->
      <input v-model="email" type="email" placeholder="E-mail" class="border w-72 py-1 pl-3">
    </form>
    <form>
      <input v-model="passWord" type="password" placeholder="비밀번호" class="border mt-3 w-72 py-1 pl-3">
    </form>
    <div>
      <button @click="login" class="border px-[121px] py-3 bg-slate-800 hover:bg-slate-700 focus:bg-slate-600 text-white m-5 relative">로그인</button>
      <p class="text-gray-400 text-sm text-right mr-5 -mt-2 cursor-pointer">아이디/비밀번호 찾기</p>
    </div>
    {{ errorMsg }}
  </div>
</template>

<script>
  import {auth} from '@/firebase'

  export default {
    name: 'LoginView',
    data() {
      return {
        email: '',
        passWord: '',
        errorMsg: '',
      }
    },
    methods: {
      login() {
        if(!this.email){
          alert('이메일을 입력해주세요')
          return false
        }
        if(!this.passWord){
          alert('비밀번호를 입력해주세요')
          return false
        }
        // .then .catch 로 바꿀수도 있음 아래는 .catch를 쓰지 않는대신 .then을 한번더 () 묶음
        auth.signInWithEmailAndPassword(this.email, this.passWord).then(((user) => {
          // console.log(user)
          // session 스토리지에 로그인 토큰 저장
          sessionStorage.setItem('refreshToken', user.user.refreshToken);
          sessionStorage.setItem('displayName', user.user.displayName);
          // /store/index.js 에서 payload 는 user.user.refreshToken 을 의미
          this.$store.commit('loginToken', {refreshToken: user.user.refreshToken, uid: user.user.uid})
          this.$router.replace('/')
        }),
        ((error)=>{
          console.log(error)
          this.errorMsg = error.message
        }))
      }
    },
  
  }
</script>

<style lang="scss" scoped>

</style>