<template>
  <div class="basis-full">
    <div class="flex gap-3 justify-between max-w-7xl mx-auto my-6 py-1 border-b">
      <h3 class="text-3xl font-bold">
        {{ BoardContent.title }}
        <span class="text-base">{{ BoardContent.name }}</span>
      </h3>
      <div class="">
        <div class="flex">
          <div class="mr-2">조회수 : {{ BoardContent.hit }}</div>
          <div @click="goodChk" class="mr-2">추천 : {{ BoardContent.good }}</div>
          <div @click="unGoodChk" class="mr-2">비추천: {{ BoardContent.bad }}</div>
        </div>
        <p class="grow text-base">{{ dateTime }}</p>
      </div>
    </div>
    <div class="mt-8 pb-5 border-b">
      <p class="my-2">
        <img v-if="BoardContent.file" :src="BoardContent.file" :alt="BoardContent.title" class="float-left block mr-3">
        {{ BoardContent.content }}
      </p>
      <p class="my-2">Lorem ipsum dolor sit amet consectetur adipisicing elit. Temporibus enim in saepe obcaecati vero minus repellendus aut! Similique dolore ea, sunt est cumque earum veritatis? Sunt ratione expedita natus quae!</p>
      <p class="my-2">Lorem ipsum dolor sit amet consectetur adipisicing elit. Temporibus enim in saepe obcaecati vero minus repellendus aut! Similique dolore ea, sunt est cumque earum veritatis? Sunt ratione expedita natus quae!</p>
    </div>
    <div class="flex justify-between pb-24 gap-x-5 mt-5">
      <router-link to="/customerservice/notice/list" 
        class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
        목록
      </router-link>
      <div class="flex gap-x-5">
        <router-link to="/customerservice/notice/modify" 
          class="bg-blue-400 hover:bg-blue-600 focus:ring-blue-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
          수정
        </router-link>
        <button 
          @click="Delete"
          class="bg-red-400 hover:bg-red-600 focus:ring-red-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
          삭제
        </button>
      </div>
    </div>

</div>
</template>

<script>
  import { db } from '@/firebase'

  export default {
    name: 'NoticeRead',
    data() {
      return {
        BoardContent: [],
        dateTime: '',
      }
    },
    mounted() {
      if(this.$route.query.docId == null) {
        this.$router.replace('/customerservice/notice/list')
      }
      db.collection('notice').doc(this.$route.query.docId).get().then((data)=>{
        this.BoardContent = data.data()
        db.collection('notice').doc(this.$route.query.docId).update({
          hit: data.data().hit + 1,
          // isUpdate: true
        })
      }).then(() => {
      db.collection('notice').doc(this.$route.query.docId).get().then((update) => {
        this.BoardContent = update.data()
        const mili = this.BoardContent.date.seconds * 1000 + this.BoardContent.date.nanoseconds / 1000000
        const date = new Date(mili);
        const result = ('' + date.getFullYear() + '-'
          + ('0' + (date.getMonth() + 1)).slice(-2) + '-'
          + ('0' + date.getDate()).slice(-2) + ' '
          + ('0' + date.getHours()).slice(-2) + ':'
          + ('0' + date.getMinutes()).slice(-2) + ':'
          + ('0' + date.getSeconds()).slice(-2)
        )
        return this.dateTime = result
      })
    })
    },
    methods: {
      Delete(){
        let msg = confirm('삭제된 데이터는 복수 할 수 없습니다. \r\r 삭제하시겠습니다?')
        if(msg){
          db.collection('notice').doc(this.$route.query.docId).delete().then(()=>{
            alert('삭제가 완료되었습니다.');
            this.$router.replace('/customerservice/notice')
          })
        }
      },
      goodChk() {
        if(this.BoardContent.goodorbadChk){
          alert('이미 추천 혹은 비추천을 하셨습니다.')
          return
        }
        db.collection('notice').doc(this.$route.query.docId).update({
          good: this.BoardContent.good + 1,
          goodorbadChk: true
        }).then(()=>{
          db.collection('notice').doc(this.$route.query.docId).get().then((data) => {
            this.BoardContent = data.data()
          })
        })
      },
      // 위에는 선생님이 작성 아래는 내가 생각했을 때 이게 더 좋을 거 같아서 수정
      unGoodChk() {
        if(this.BoardContent.goodorbadChk){
          alert('이미 추천 혹은 비추천을 하셨습니다.')
          return
        }
        this.BoardContent.bad = this.BoardContent.bad + 1
        db.collection('notice').doc(this.$route.query.docId).update({
          bad: this.BoardContent.bad,
          goodorbadChk: true

        })
      }
    },
   }
</script>

<style lang="scss" scoped>

</style>