<template>
  <div class="basis-full">
    <div class="max-w-7xl mx-auto my-10">
      <p class="text-right">
        <button @click="filterMine = !filterMine"
          class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 mb-2 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
          <template v-if="!filterMine">나의 글 보기</template>
          <template v-if="filterMine">모든 글 보기</template>
        </button>
      </p>
      <ul class="flex justify-between border-indigo-500 border-t-2 border-b-[1px] p-4 py-2 bg-indigo-50 text-center font-bold">
        <li class="basis-1/12">번호</li>
        <li class="basis-4/12">제목</li>
        <li class="basis-2/12">글쓴이</li>
        <li class="basis-2/12">날짜</li>
        <li class="basis-1/12">조회수</li>
        <li class="basis-1/12">추천</li>
        <li class="basis-1/12">비추천</li>
      </ul>
      <ul v-for="(e, index) in filterDataList" :key="index" 
        class="flex justify-between border-b text-center px-4 py-2 even:bg-gray-50 text-xs sm:text-sm hover:font-bold">
        <!--  -->
        <li class="basis-1/12">{{ filterDataList.length - index }}</li>
        <li class="basis-4/12">
          <router-link 
            @click="$store.commit('OnlineRead', dataId[index])" 
            :to="{ name: 'OnlineRead', query: { docId: dataId[index] } }">
            <font-awesome-icon v-if="e.secret" icon="user-lock" />   
            {{ e.secret ? '비밀 글 입니다.' : e.title }}
          </router-link>
        </li>
        <li class="basis-2/12">
          {{ e.secret ? '' : e.name }}
          <font-awesome-icon v-if="e.secret" icon="user-lock" />
        </li>
        <li class="basis-2/12">{{ BoardDate(index) }}</li>
        <li class="basis-1/12">{{ e.hit }}</li>
        <li class="basis-1/12">{{ e.good }}</li>
        <li class="basis-1/12">{{ e.bad }}</li>
      </ul>
    </div>
    <div
      v-if="$store.state.loginToken"
        class="flex justify-end pb-24">
      <router-link to="/customerservice/online/write" 
        class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
        글쓰기
      </router-link>
    </div>
  </div>
</template>

<script>
   import { db } from '@/firebase'

  export default {
    name: 'OnlineList',
    data() {
      return {
        dataList: [],
        dataId: [],
        filterMine: false,
        uid: '',
      }
    },
    computed: {
      filterDataList() {
        if(this.filterMine == true){
          return this.dataList.filter((data)=>{
            return data.uid == this.$store.state.uid
          }) 
        }
        return this.dataList
      }
    },
    methods: {
      BoardDate(index) {
        const date = this.dataList[index].date.seconds * 1000 + this.dataList[index].date.nanoseconds / 1000000
        const formattingDate = new Date(date).toLocaleString()
        const cutLine = formattingDate.indexOf('오')
        return formattingDate.substring(0, cutLine)
      },
    },
    mounted() {
      db.collection('online').orderBy('date', 'desc').limit(10).get().then((data) => {
        data.forEach((e) => {
          this.dataId.push(e.id)
          this.dataList.push(e.data())
        })
      })
    },
  }
</script>