<template>
  <div class="basis-full relative">
      <div class="max-w-7xl mx-auto my-10">
        <label class="block w-full">
          <input v-model="title" type="text" placeholder="질문"  class="text-4xl w-full border block mx-auto">
        </label>
        <textarea v-model="content" class="mt-3 border w-full" cols="100" rows="10" placeholder="답변"></textarea>
      </div>
      <button 
        @click="write"
        class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm absolute right-0 bottom-0">
        완료
      </button>
    </div>
</template>

<script>
  import { db } from '@/firebase'

  export default {
    name: 'QnaWrite',
    data() {
      return {
        title: '',
        content: '',
        date: new Date(),
      }
    },
    methods: {
      write(){
        if(this.$store.state.uid == this.$store.state.adminUid) {
          db.collection('qna').add({
           'title': this.title,
           'content': this.content,
           'date': this.date,
           'uid': this.$store.state.uid
          })
        } else {
          alert('작성 권한이 없습니다.')
        }
        this.$router.replace('/customerservice/qna')
      }
    },
  }
</script>