<template>
  <div class="basis-full">
    <div class="max-w-7xl mx-auto my-10">
      <ul class="flex justify-between border-indigo-500 border-t-2 border-b-[1px] p-4 py-2 bg-indigo-50 text-center font-bold">
        <li class="basis-1/12">번호</li>
        <li class="basis-4/12">제목</li>
        <li class="basis-2/12">글쓴이</li>
        <li class="basis-2/12">날짜</li>
        <li class="basis-1/12">조회수</li>
        <li class="basis-1/12">추천</li>
        <li class="basis-1/12">비추천</li>
      </ul>
      <template v-for="(e, index) in dataList" :key="index" >
        <ul v-if="calculateNumber(totalLength, perPage, page, index) > 0"
          class="flex justify-between border-b text-center px-4 py-2 even:bg-gray-50 text-xs sm:text-sm hover:font-bold">
          <!--  -->
          <li class="basis-1/12">{{calculateNumber(totalLength, perPage, page, index)}}</li>
          <li class="basis-4/12">
            <router-link 
              @click="$store.commit('NoticeRead', dataId[index])" 
              :to="{name: 'NoticeRead', query: {docId: dataId[index]}}">{{ e.title }}
            </router-link>
          </li>
          <li class="basis-2/12">{{e.name}}</li>
          <li class="basis-2/12">{{ BoardDate(index) }}</li>
          <li class="basis-1/12">{{ e.hit }}</li>
          <li class="basis-1/12">{{ e.good }}</li>
          <li class="basis-1/12">{{ e.bad }}</li>
        </ul>
      </template>
    </div>
    <div
      v-if="$store.state.loginToken"
       class="flex justify-end pb-24">
      <router-link to="/customerservice/notice/write" 
        class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
        글쓰기
      </router-link>
    </div>
    <div class="flex justify-center basis-full mb-5 gap-3">
      <button @click="prevPage" :disabled="currentPage <= 1">이전</button>
      <button @click="goToPage(e)" v-for="e in pageCount.pagination" :key="e" class="" :class="page == e && 'font-bold text-orange-500'">{{ e }}</button>
      <button @click="nextPage" :disabled="currentPage > pageCount.totalPage / block">다음</button>
    </div>
  </div>


</template>

<script>
  import {db} from '@/firebase'

  export default {
    name: 'NoticeList',
    data() {
      return {
        dataList: [],
        dataId: [],
        posts: [],
        page: 1,
        perPage: 5,
        lastVisible: null,
        totalLength: 0,
        block: 5,
        currentPage: 1
      }
    },
    computed: {
      pageCount(){
        // 전체 페이지 수 계산 (전체 게시물 / 페이지 당 게시물 수)
        const totalPage = Math.ceil(this.totalLength / this.perPage)
        // 현재 페이지에 보여질 게시물 시작 인덱스
        const start = (this.currentPage - 1) * this.perPage
        // 현재 페이지에 보여질 게시물 마지막 인덱스
        const end = start + this.block
        const pagination = []
        for(let i = start + 1; i <= end && i <= totalPage; i++){
          pagination.push(i)
        }
        // 만약 페이지 번호 배열이 비어있고 전체 페이지 수가 0보다 크다면 마지막 페이지 번호만 배열에 추가
        if(pagination.length === 0 && totalPage > 0){
          pagination.push(totalPage)
        }
        return {totalPage, start, end, pagination}
      }
    },
    methods: {
      BoardDate(index){
        const date = this.dataList[index].date.seconds * 1000 + this.dataList[index].date.nanoseconds/1000000
        const formattingDate = new Date(date).toLocaleString()
        const cutLine = formattingDate.indexOf('오')
        return formattingDate.substring(0, cutLine)
      },
      fetchTotalLength(){
        db.collection('notice').get().then((data)=>{
          // 모든 notice 문서를 가지고 와서 개수를 지정
          this.totalLength = data.size
        })
      },
      fetchPost(){
        let query = db.collection('notice').orderBy('date', 'desc').limit(this.perPage)

        if(this.page > 1 && this.lastVisible){
          query = query.startAfter(this.lastVisible);
        }

        // console.log('lastVisible id 는', this.lastVisible?.id)
        
        query.get().then((data)=>{
          const items = []
          const ids = []

          data.forEach(e=>{
            ids.push(e.id)
            items.push(e.data())
          })

          this.dataId = ids
          this.dataList = items
          this.lastVisible = data.docs[data.docs.length - 1]

          // data.forEach(e => console.log(e.id))

        })
      },
      calculateNumber(total, per, p, index){
        // 현재 페이지 이전 게시물 수
        const totalPages = per * (p - 1)
        // 게시물 인덱스 계산
        const postIndex = total - (totalPages + index)
        // 계산된 게시물 인덱스가 0보다 작거나 같으면 종료 
        if(postIndex <= 0){return}
        return postIndex
      },
      goToPage(e){
        // if(this.page == e){return}
        this.page = e
        // console.log('lastVisible  : ', e)
        this.fetchPost()
      },
      prevPage(){
        this.currentPage = this.currentPage - 1
        this.page = this.pageCount.pagination[0]
        this.fetchPost()
      },
      nextPage(){
        
        // 현재 블록 페이지 번호를 1 증감 시킴
        this.currentPage = this.currentPage + 1
        // 배열에 추가된 첫번째 번호 페이지 이동
        this.page = this.pageCount.pagination[0]
        /* // 위코드 대신 아래 코드로 사용하면 한페이지씩 다음이동 됨
        this.page++ */
        // 이동된 페이지의 게시글을 다시 호출
        this.fetchPost()
      }
    },
    mounted() {
      this.fetchTotalLength()
      this.fetchPost()
    },
  }
</script>

<style lang="scss" scoped>

</style>