<!-- eslint-disable no-mixed-spaces-and-tabs -->
<template>
  <div class="w-full py-24 text-center text-white bg-[url('https://via.placeholder.com/1920x960/007')] bg-fixed bg-center">
    <div ref="content" class="mb-6">
      <h3 class="text-2xl uppercase">diffrent</h3>
      <p class="opcity-90">제목에 관한 부가 설명</p>
    </div>
    <div class="max-w-7xl  mx-auto py-12 px-[2%]">
      <ul class="flex justify-between flex-wrap">
        <li v-for="(e, index) in Different" :key="index" class="basis-full sm:basis-2/4 lg:basis-1/4 text-center">
          <p class="text-xl">{{ e.title }}</p>
          <!-- vue-number-animtion 활용 -->
          <number
          tag="p"
          :ref="`number${index+1}`"
          :to="e.number"
      	  :format="theFormat"
      	  :duration="5"
          animationPaused
          easing="Power1.easeOut"
          class="text-6xl pt-5 block" />
          <p class="text-lg pb-8">{{ e.en }}</p>
        </li>
      </ul>
    </div>
    

  </div>
</template>

<script>
  import DifferentData from '@/assets/data.json'

  export default {
    name: 'HomeDifferent',
    data() {
      return {
        Different: DifferentData.Diffrent,
        windowScroll: window.scrollY,
      }
    },
    methods: {
      theFormat(number) {
        // return number.toFixed(0);
        // 3자리 수 구분하기 위해 toLocaleString
        return Math.round(number).toLocaleString()
      },
      updatescroll() {
        if(this.$refs.content){
          this.windowScroll = window.scrollY
          // 맨아래에서 600에 접근하면 애니메이션 시작하기 위해 빼기 600
          const ContentOffset = this.$refs.content.offsetTop-600
          // console.log(ContentOffset)
          /* if(this.$refs.content) 넣어준 이유는 offset은 위치를 못잡아서 content 위치를 잡은 경우에만 실행하도록 하기 */
          if(ContentOffset < this.windowScroll){
            this.$refs.number1[0].play()
            this.$refs.number2[0].play()
            this.$refs.number3[0].play()
            this.$refs.number4[0].play()
          }
        }
      }
    },
    mounted() {
      window.addEventListener('scroll', this.updatescroll)
    },
  }
</script>

