<template>
  <div>
    <swiper class="h-auto sm:h-[640px]" 
      effect='fade' 
      :modules="Modules" 
      :autoplay="true && {delay: 3000}" 
      :loop="true" 
      :navigation="{ prevEl: '.swiper-button-prev', nextEl: '.swiper-button-next'}" 
      :pagination="{ clickable: true, type: 'fraction'}"
      @slideChange="onSlideChange">
      <swiper-slide 
        v-for="(e, index) in 5" :key="index" 
        class="relative">
        <img :src="require(`@/assets/images/banner-${index+1}.png`)" :alt="`e`" class="w-full h-full">
        <div class="absolute left-2/4 top-2/4 -translate-x-2/4 -translate-y-2/4 text-white">
          <h3 data-wow-duration="1s" class="wow animate__fadeInDown text-center text-base sm:text-3xl lg:text-5xl transition-all">강조하는 제목 {{ index + 1 }}</h3>
          <p data-wow-duration="1s" class="wow animate__fadeInUp text-sm text-center lg:text-2xl sm:text-xl font-bold mt-4">부가적인 설명 영역</p>
          <p data-wow-duration="1s" class="wow animate__fadeInUp text-sm text-center lg:text-2xl sm:text-xl font-bold ">내용이 더 있다면 보여주기 ...</p>
          <router-link to="/" data-wow-duration="1s" class="wow animate__fadeInUp bg-slate-400 px-10 py-2 block mx-auto w-32 mt-4 rounded-lg text-sm lg:text-base">More</router-link>
        </div>
      </swiper-slide>
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>
    </swiper>
  </div>
</template>

<script>
import {Swiper, SwiperSlide} from 'swiper/vue'
import { Navigation, EffectFade, Autoplay, Pagination } from 'swiper';

import 'swiper/css'
import 'swiper/css/bundle'
import 'swiper/css/pagination'
import 'swiper/css/navigation'

import WOW from 'wowjs'
import 'animate.css'


  export default {
    name: 'HomeBanner',
    data() {
      return {
        // [] 주의
        Modules: [Navigation, EffectFade, Pagination, Autoplay]
      }
    },
    components: {
      Swiper, 
      SwiperSlide
    },
    methods: {
      // slide가 바뀌면 함수 실행 swiper에서 슬라이드가 변경이 되었을때 주려고 하는 함수는 @slideChange 로 사용하고 함수 호출하여야 함
      // 안에 함수 내용은 글자가 슬라이드 바뀔때마다 올라오거나 내려가는 듯한 애니메이션을 주는 함수
      onSlideChange() {
        new WOW.WOW({
          live: false
        }).init()
      }
    },
    mounted() {
      /* wowjs https://wowjs.uk/docs.html */
      new WOW.WOW({
        boxclass: 'wow',
        animateClass: 'animate__animated',
        live: false,
        mobile: true
      }).init();
    },
  }
</script>

<style>

  .swiper-pagination.swiper-pagination-fraction{
    background: rgba(255,255,255,0.5);
    width: auto;
    left: 50%;
    transform: translateX(-50%);
    padding: 2px 35px;
    letter-spacing: 3px;
    border-radius: 20px;
    font-weight: bold;
    color: white;
  }

  @media screen and (max-width: 1024px) {
    .swiper-pagination.swiper-pagination-fraction,
    .swiper-pagination.swiper-pagination-fraction span{
      font-size: 14px;
    }
    .swiper-button-next::after,
    .swiper-button-prev::after{
      font-size: 14px;
  }
  }

  @media screen and (max-width: 640px) {
    .swiper-pagination.swiper-pagination-fraction,
    .swiper-pagination.swiper-pagination-fraction span{
      font-size: 10px;
    }
  }

  .swiper-button-next::after,
  .swiper-button-prev::after{
    color: white;
  }

</style>