<template>
  <div class="sticky top-0 w-full bg-white border-b border-b-white/30 z-40">
    <div class="max-w-7xl mx-auto flex justify-between items-center py-2.5 px-[2%] ">
      <div><router-link to="/"><img src="https://via.placeholder.com/120x60" alt="로고"></router-link></div>
      <div class="hidden lg:flex justify-between basis-2/3">
        <ul class="flex justify-between basis-2/3">
          <li 
            @mouseover="NavSelectIndex = index; SubMenu(index)" 
            @mouseout="NavSelectIndex = null"
            v-for="(Nav, index) in NavList[0]" :key="index" 
            :class="NavSelectIndex === index && 'font-bold'"
            class="relative basis-1/4 text-center">
            <router-link :to="NavList[1][index]" class="text-base hover:font-bold">
              {{ Nav }} 
              <font-awesome-icon icon="chevron-down" 
                :class="NavSelectIndex === index && 'rotate-180'" 
                class="transition-all duration-500 text-xs align-baseline" />
            </router-link>
            <ul 
              :style="NavSelectIndex === index && subMenuHeight"
              class="w-36 absolute bg-slate-800 h-0 overflow-hidden transition-all duration-500 nav_sub">
              <li
                v-for="(submenu, i) in SubList[index]" :key="submenu" 
                class="py-2.5 font-normal">
                <router-link :to="SubMenulink[index][i]" 
                  class="text-sm text-white/90 hover:font-bold">{{ submenu }}
                </router-link>
              </li>
            </ul>
          </li>
        </ul>
        <router-link to="/admin">admin 테스트</router-link>
        <ul class="flex gap-x-5">
          <li v-if="!$store.state.loginChk">
            <router-link to="/login" class="text-sm">
              <font-awesome-icon icon="lock" /> 로그인
            </router-link>
          </li>
          <li @click="logOut" v-else-if="$store.state.loginChk">
            <router-link to="/" class="text-sm">
              <font-awesome-icon icon="arrow-right-from-bracket" /> 로그아웃
            </router-link>
          </li>
          <li v-if="!$store.state.loginChk ">
            <router-link to="/member" class="text-sm">
              <font-awesome-icon icon="user" /> 회원가입
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <!-- 1024 보다 작을 때 햄버거 메뉴 -->
    <div 
        @click="isOpen = isOpen ? false : true"
        :class="isOpen && 'hamburgeron'"
        class="overflow-hidden absolute right-4 md:right-8 top-6 duration-1000 transition-all z-50 cursor-pointer lg:hidden">
        <div v-for="e in 3" :key="e" class="w-[30px] h-0.5 bg-black rounded m-1.5 transition-all duration-1000"></div>
    </div>
    <!-- 1024 보다 작을 때 서브메뉴  -->
    <div 
      :class="isOpen && '!right-0'"
      class="w-80 h-full fixed bg-gray-50 z-40 -right-80 p-12 box-border transition-all duration-700 top-0 lg:hidden overflow-y-scroll">
      <ul class="mt-12">
        <li 
          @click="NavSelectIndex = index; SubMenu(index)" 
          v-for="(e, index) in NavList[0]" :key="e" 
          class="py-5 border-b relative cursor-pointer font-bold">
          {{ e }} 
          <font-awesome-icon icon="chevron-down" 
            :class="NavSelectIndex === index && RotateClass" 
            class="transition-all duration-500 text-xs align-baseline absolute right-5 top-6" />
          <template v-for="(el, i) in SubList" :key="el" >
            <!-- style과 style 바인딩 순서 바꾸면 2번 클릭 해야 작동됨 -->
            <ul 
              v-if="index === i" 
              style="height: 0px;" 
              :style="NavSelectIndex === index && `height: ${SubHeight}`" 
              class="flex flex-wrap justify-between sub_list overflow-hidden duration-300" 
              >
              <li v-for="(ele,index2) in el" :key="ele" class="basis-full text-xs hover:text-[#37a2d9]">
                <router-link
                  @click="isOpen=false"
                  class="first-of-type:pt-5 pl-5 pt-2 inline-block w-full"
                 :to="SubMenulink[i][index2]">{{ ele }}</router-link>
              </li>
            </ul>
          </template>
        </li>
      </ul>
    </div>

  </div>
</template>

<script>
import NavData from '@/assets/data.json'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { auth } from '@/firebase'

export default {
  name: "NavComponent",
  data() {
    return {
      NavList: NavData.Nav,
      SubList: NavData.SubMenu,
      SubMenulink: NavData.SubMenuLink,
      NavSelectIndex: null,
      isOpen: false,
      SubHeight: '0px',
      RotateClass: '',
      windowWidth: 0
    }
  },
  components: { 
    FontAwesomeIcon 
  },
  methods: {
    // 반응형을 위해서 사용 mounted 에서 매서드 호출하여 놓음
    windowUpdateWidth(){
      this.windowWidth = window.innerWidth;
    },
    SubMenu(e) {
      const list = document.querySelectorAll('.sub_list')[e];
      const length = list.querySelectorAll("li").length;

      // 1024보다 클 때 상단 서브메뉴
      this.subMenuHeight = `height:${length * 44}px` 


      // 1024보다 작을 때 우측 서브메뉴
      if (list.style.height === '0px') {
        list.style.height = `${length * 40}px`
        this.RotateClass = 'rotate-180'
      } else {
        list.style.height = '0'
        this.RotateClass = ''
      }
      this.SubHeight = list.style.height;
    },
    logOut() {
      auth.signOut();
      this.$store.commit('logOutToken')
    }
  },
  // watch 데이터 변경이 요소가 변경(함수)을 체크
  // computed는 값이 변하는것을 체크
  watch: {
    // 아래 windowWidth 가 없다면 1024 보다 작을 때 메뉴를 클릭 한 상태에서 화면을 늘리면 서브메뉴가 내려가 있음
    
    // watch가 없어도 되네 왜 되는거지????????
    // windowWidth(width){
    //   if(width < 1024){
    //     this.NavSelectIndex = null;
    //     this.isOpen = false;
    //   }
    // }
  },
  mounted() {
    window.addEventListener('resize', this.windowUpdateWidth)
  },
}
</script>

<style>
/* 햄버거 메뉴 */
.hamburgeron div:nth-child(1){ transform: rotate(45deg) translateY(12px) }
.hamburgeron div:nth-child(2){ opacity: 0; transform: translateX(30px) }
.hamburgeron div:nth-child(3){ transform: rotate(-45deg) translateY(-12px) }
</style>