<template>
  <Nav />
  <router-view />
  <Footer />
</template>

<script>
import {auth} from '@/firebase'

import Nav from './components/Nav.vue';
import Footer from './components/Footer.vue';

export default {
  components: {
    Nav, Footer
  },
  mounted() {
    auth.onAuthStateChanged((user)=>{
      // 로그인이 되어있다면
      if(user) {
        this.$store.commit('loginState', {displayName: user.displayName, uid: user.uid});
      }
    })
  },
}
</script>