<template>
  <div class="py-24 text-center basis-full">
    <h3 class="text-3xl font-bold mb-5 relative after:absolute inline-block after:w-[80%] after:h-0.5 after:bg-slate-800 after:-bottom-2 after:left-2/4 after:-translate-x-2/4">회원가입</h3>
  </div>
  <div class="pb-24 flex flex-col items-center">
    <form class="">
        <input v-model="nickname" type="text" placeholder="닉네임" class="border w-72 py-1">
    </form>
    <form class="">
        <input v-model="email" type="text" placeholder="E-mail" class="border w-72 py-1 mt-3">
    </form>
    <form>
        <input v-model="passWord" type="password" placeholder="비밀번호" class="border mt-3 w-72 py-1">
    </form>
    <!-- <form>
        <input type="password" placeholder="비밀번호 확인" class="border mt-3 w-72 py-1">
    </form> -->
    <div>
      <button 
        @click="signUp" 
        class="border px-[121px] py-3 bg-slate-800 hover:bg-slate-700 focus:bg-slate-600 text-white m-5 relative">
        회원가입
      </button>
    </div>
  </div>

</template>

<script>
  import { auth } from '@/firebase';

  export default {
    name: 'MemberView',
    data() {
      return {
        email: '',
        passWord: '',
        nickname: ''
      }
    },
    methods: {
      signUp() {
        auth.createUserWithEmailAndPassword(this.email, this.passWord).then((result)=>{
          // console.log(result)
          // console.log(result.user)
          // 닉네임은 아래처럼 하여 반영
          result.user.updateProfile({displayName: this.nickname})
          alert('회원가입이 완료되었습니다. 로그인페이지로 이동합니다.')
          this.$router.replace('/login')
        })
      }
    },
  }
</script>

