<template>
  <div class="basis-full relative">
    <div class="max-w-7xl mx-auto my-10">
      <label class="block max-w-7xl">
        <input v-model="title" type="text" placeholder="제목"  class="text-4xl max-w-7xl border block mx-auto">
      </label>
      <label class="text-right block">
        이름을 입력해주세요
        <input v-model="name" type="text" placeholder="이름"  class="border">
      </label>
      <textarea v-model="content" class="mt-3 border w-full" cols="100" rows="10"></textarea>
      <label class="mr-3">
        공개 여부
        <input type="checkbox" v-model="secret">
      </label>
      <input type="file" id="image" class="shadow appearance-none border rounded- w-8/12 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline">
    </div>
    <button 
      @click="write"
      class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm absolute right-0 bottom-0">
      완료
    </button>
  </div>
</template>

<script>
  import { db, storage } from '@/firebase'

  export default {
    name: 'OnlineWrite',
    data() {
      return {
        title: '',
        content: '',
        name: this.$store.state.displayName,
        date: new Date(),
        file: '',
        secret: false
      }
    },
    methods: {
      write() {
        if (!this.title) {
          alert('title을 입력해주세요')
        } else {
          this.file = document.querySelector('#image').files[0]

          // firebase database 에 쓰기
          db.collection('online').add({
            'name': this.name,
            'title': this.title,
            'content': this.content,
            'date': this.date,
            'hit': 1,
            'good': 0,
            'bad': 0,
            'uid': this.$store.state.uid,
            'secret': this.secret
          })
          // firebase storage 에 저장하기
          if (this.file) {
            storage.ref().child('images/' + this.NamingDate()).put(this.file)
          }
          // 쓰고 페이지 이동
          this.$router.replace('/customerservice/online')
        }
      },
      NamingDate() {
        const date = new Date();
        const RanNum = Math.floor(Math.random() * 999) + 100;
        const result = ('' + date.getFullYear() + ''
          + ('0' + (date.getMonth() + 1)).slice(-2) + ''
          + ('0' + date.getDate()).slice(-2) + '-'
          + ('0' + date.getHours()).slice(-2) + ''
          + ('0' + date.getMinutes()).slice(-2) + ''
          + ('0' + date.getSeconds()).slice(-2) + '-' + RanNum
        )
        return result
      }
    }
  }
</script>