import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginView from '../views/LoginView.vue'
import MemberView from '../views/MemberView.vue'

// https://wordpresser.co.kr/4315/
/* 1뎁스 */
import CompanyView from '../views/CompanyView.vue'
import BusinessView from '../views/BusinessView.vue'
import ProductView from '../views/ProductView.vue'
import CustomerView from '../views/CustomerView.vue'
/* company 2뎁스 */
import GreetingsView from '../views/Company/GreetingView.vue'
import DirectionsView from '../views/Company/DirectionsView.vue'
import HistoryView from '../views/Company/HistoryView.vue'
import InteriorView from '../views/Company/InteriorView.vue'
/* business 2뎁스 */
import BusinessView_1 from '../views/Business/BusinessView_1.vue'
import BusinessView_2 from '../views/Business/BusinessView_2.vue'
import BusinessView_3 from '../views/Business/BusinessView_3.vue'
import BusinessView_4 from '../views/Business/BusinessView_4.vue'
/* product 2뎁스 */
import ProductView_1 from '../views/Product/ProductView_1.vue'
import ProductView_2 from '../views/Product/ProductView_2.vue'
import ProductView_3 from '../views/Product/ProductView_3.vue'
/* customerservice 2뎁스 */
import NoticeView from '../views/Customerservice/NoticeView.vue'
import OnlineView from '../views/Customerservice/OnlineView.vue'
import QnaView from '../views/Customerservice/QnaView.vue'
import GallaryView from '../views/Customerservice/GallaryView.vue'

/* customerservice notice 3뎁스 */
import NoticeList from '../views/Customerservice/NoticeList'
import NoticeModify from '../views/Customerservice/NoticeModify'
import NoticeRead from '../views/Customerservice/NoticeRead'
import NoticeWrite from '../views/Customerservice/NoticeWrite'

/* customerservice Online 3뎁스 */
import OnlineList from '../views/Customerservice/OnlineList'
import OnlineModify from '../views/Customerservice/OnlineModify'
import OnlineRead from '../views/Customerservice/OnlineRead'
import OnlineWrite from '../views/Customerservice/OnlineWrite'


/* customerservice Qna 3뎁스 */
import QnaList from '../views/Customerservice/QnaList'
import QnaModify from '../views/Customerservice/QnaModify'
import QnaRead from '../views/Customerservice/QnaRead'
import QnaWrite from '../views/Customerservice/QnaWrite'

import AdminView from '../views/AdminView'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/member',
    name: 'member',
    component: MemberView
  },
  {
    path: '/company',
    name: 'company',
    component: CompanyView, 
    /* 리다이렉팅 (/company 으로 들어오면 /company/greetings 으로 강제 이동 ) */
    redirect: "/company/greetings",
    /* 중첩 라우팅 */
    children: [
      {
        path: '/company/greetings',
        name: 'greetings',
        component: GreetingsView
      },
      {
        path: '/company/history',
        name: 'history',
        component: HistoryView
      },
      {
        path: '/company/interior',
        name: 'interior',
        component: InteriorView
      },
      {
        path: '/company/direction',
        name: 'direction',
        component: DirectionsView
      }
    ]
  },
  {
    path: '/business',
    name: 'business',
    component: BusinessView,
    redirect: "/business/1",
    /* 중첩 라우팅 */
    children: [
      {
        path: '/business/1',
        name: 'business_1',
        component: BusinessView_1
      },
      {
        path: '/business/2',
        name: 'business_2',
        component: BusinessView_2
      },
      {
        path: '/business/3',
        name: 'business_3',
        component: BusinessView_3
      },
      {
        path: '/business/4',
        name: 'business_4',
        component: BusinessView_4
      },
    ]
  },
  {
    path: '/product',
    name: 'product',
    component: ProductView,
    redirect: "/product/1",
    children: [
      {
        path: '/product/1',
        name: 'ProductView_1',
        component: ProductView_1
      },
      {
        path: '/product/2',
        name: 'ProductView_2',
        component: ProductView_2
      },
      {
        path: '/product/3',
        name: 'ProductView_3',
        component: ProductView_3
      }
    ]
  },
  {
    path: '/customerservice',
    name: 'customerservice',
    redirect: '/customerservice/notice',
    component: CustomerView,
    children: [
      {
        path: '/customerservice/notice',
        name: 'NoticeView',
        component: NoticeView,
        redirect: '/customerservice/notice/list',
        children: [
          {
            path: '/customerservice/notice/list',
            name: 'NoticeList',
            component: NoticeList
          },
          {
            path: '/customerservice/notice/write',
            name: 'NoticeWrite',
            component: NoticeWrite
          },
          {
            path: '/customerservice/notice/read',
            name: 'NoticeRead',
            component: NoticeRead
          },
          {
            path: '/customerservice/notice/modify',
            name: 'NoticeModify',
            component: NoticeModify
          },
        ]
      },
      {
        path: '/customerservice/online',
        name: 'OnlineView',
        component: OnlineView,
        redirect: '/customerservice/online/list',
        children: [
          {
            path: '/customerservice/online/list',
            name: 'OnlineList',
            component: OnlineList
          },
          {
            path: '/customerservice/online/write',
            name: 'OnlineWrite',
            component: OnlineWrite
          },
          {
            path: '/customerservice/online/read',
            name: 'OnlineRead',
            component: OnlineRead
          },
          {
            path: '/customerservice/online/modify',
            name: 'OnlineModify',
            component: OnlineModify
          },
        ]
      },
      {
        path: '/customerservice/qna',
        name: 'QnaView',
        component: QnaView,
        redirect: '/customerservice/qna/list',
        children: [
          {
            path: '/customerservice/qna/list',
            name: 'QnaList',
            component: QnaList
          },
          {
            path: '/customerservice/qna/write',
            name: 'QnaWrite',
            component: QnaWrite
          },
          {
            path: '/customerservice/qna/read',
            name: 'QnaRead',
            component: QnaRead
          },
          {
            path: '/customerservice/qna/modify',
            name: 'QnaModify',
            component: QnaModify
          },
        ]
      },
      {
        path: '/customerservice/gallary',
        name: 'GallaryView',
        component: GallaryView
      },
      {
        path: '/admin',
        name: 'AdminView',
        component: AdminView,
        // 관리자만 들어가야할 페이지에는 아래 코드 꼭 필요
        meta: { manager: true }
      },
    ]
  },
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

// ===============================================================================================
// 코드 어디에 적어요? -> router폴더 index.js

import store from "@/store";


router.beforeEach((to, from, next) => {
  // to.meta.manager 있어야 함 없으면 모든 페이지에서 관리자 접근 문제 일으킴

  // 참고 router로 이동하는 거라서 주소창으로 직접 이동하면 막혀버림
  if (to.meta.manager && store.state.chkManager == false) {
    alert('관리자만 접근이 가능합니다.');
    next('/');
    return;
  }
  next();
});

// ===============================================================================================


export default router
