import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import "./index.css"
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faArrowRightFromBracket, faBuilding, faChevronCircleDown, faChevronDown, faCircleArrowUp, faComment, faDesktop, faHistory, faLock, faMedal, faMobile, faPaperPlane, faTaxi, faUser, faUserLock, faUserSecret } from '@fortawesome/free-solid-svg-icons'
import VueNumber from 'vue-number-animation'

library.add(faChevronCircleDown, faBuilding, faHistory, faPaperPlane, faMedal, faLock, faUser, faArrowRightFromBracket, faCircleArrowUp, faChevronDown, faDesktop, faComment, faTaxi, faMobile, faUserSecret, faUserLock)


createApp(App).component('font-awesome-icon', FontAwesomeIcon).use(VueNumber).use(store).use(router).mount('#app')
