<template>
  <h3>수정</h3>
  <div class="basis-full relative">
    <div class="max-w-7xl mx-auto my-10">
      <label class="block max-w-7xl">
        <input v-model="title" type="text" placeholder="제목"  class="text-4xl max-w-7xl border block mx-auto">
      </label>
      <label class="text-right block">
        이름을 입력해주세요
        <input v-model="name" type="text" placeholder="이름"  class="border">
      </label>
      <textarea v-model="content" class="mt-3 border w-full" cols="100" rows="10"></textarea>
    </div>
    <button 
      @click="modify"
      class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm absolute right-0 bottom-0">
      수정 완료
    </button>
 </div>
</template>

<script>
  import {db} from '@/firebase'

  export default {
    name: 'NoticeModify',
    data() {
      return {
        BoardContent: [],
        name: this.$store.state.displayName,
        title: '',
        content: '',
        date: new Date(),
        oriDate: '',
      }
    },
    mounted() {
      if (!this.$store.state.noticeId) {
        this.$router.replace('/customerservice/notice')
      }
      db.collection('notice').doc(this.$store.state.noticeId).get().then((data) => {
        this.BoardContent = data.data()
        this.name = this.BoardContent.name
        this.title = this.BoardContent.title
        this.content = this.BoardContent.content
        this.oriDate = this.BoardContent.date
      })
    },
    methods: {
      modify() {
        db.collection('notice').doc(this.$store.state.noticeId).update({
          "name": this.name,
          "title": this.title,
          "content": this.content
        }).then(()=>{
          alert('수정이 완료되었습니다.')
          this.$router.replace('/customerservice/notice')
        })
      }
    },
    computed: {
      formattedDate(){
        const date = this.oriDate.seconds*1000 + this.oriDate.nanoseconds/1000000
        const formattingDate = new Date(date).toLocaleString()
        const cutLine = formattingDate.indexOf('오')
        return formattingDate.substring(0, cutLine)
      }
    }
  }
</script>

