import { createStore } from 'vuex'

export default createStore({
  // 데이터가 하나가 바뀌면 다른 컴포넌트도 같이 변경 
  // 자주 바뀌지 않는 데이터라면 vuex 보다 자체 컴포넌트에 데이터로 활용하는 것을 권장
  state() {
    return {
      number: 0,
      loginChk: false,
      displayName: '',
      loginToken: null,
      noticeId: '',
      onlineId: '',
      qnaId: '',
      uid: '',
      adminUid: 'gb5T60JLBBdNpcFdZIp1MUQ9dbu1',
      // ===============================================================================================
      // 코드 어디에 적어요? -> store폴더 index.js
      chkManager: false

      // ===============================================================================================
    }
  },
  // 공통적인 함수사용
  mutations: {
    // state 를 매개변수로 넣어 접근
    numberPlus(state) {
      state.number++;
    },
    numberMinus(state) {
      state.number--;
    },
    // LoginView.vue
    loginToken(state, payload) {
      state.loginToken = payload.refreshToken,
      state.loginChk = true
      state.uid = payload.uid
    },
    // Nav.vue
    logOutToken(state){
      state.loginChk = false;
      state.loginToken = null;
      state.displayName = '';
      state.uid = '';
      sessionStorage.removeItem('refreshToken')
      sessionStorage.removeItem('displayName')
    },
    // App.vue
    loginState(state, payload){
      state.loginToken = sessionStorage.getItem('refreshToken');

      state.uid = payload.uid

      if (state.loginToken) {
        state.uid = payload.uid;
        state.displayName = payload.displayName;
        state.loginChk = true
      }
      if (state.uid == state.adminUid) {
        state.chkManager = true
      }
    },
    // NoticeList.vue 
    NoticeRead(state, payload) {
      state.noticeId = payload
    },
    // OnlineList.vue
    OnlineRead(state, payload) {
      state.onlineId = payload
    },
    QnaModify(state, payload) {
      state.qnaId = payload
    }
  },
  actions: {
  },
  // 아래 코드 활성화 하면 콘솔창에 뜸
  // plugins: [createLogger()]
})
